'use client';

import { useState, useEffect } from 'react';
import { endpointsApi } from '@/lib/api-client';
import { ApiError } from '@/lib/api-client';

interface UsageLog {
  id: string;
  endpointId: string;
  timestamp: string;
  statusCode: number;
  responseTime: number;
  conversationId?: string;
  agentId?: string;
  x402TxHash?: string;
  failureCode?: string;
}

interface OperationsStats {
  totalRequests: number;
  successfulRequests: number;
  failedRequests: number;
  totalRevenue: string;
  averageLatency: number;
  requestsByHour: Array<{ hour: string; count: number }>;
  failuresByCode: Record<string, number>;
  recentTransactions: Array<{
    txHash: string;
    timestamp: string;
    amount: string;
    customerId: string;
  }>;
}

export default function OperationsPage() {
  const [stats, setStats] = useState<OperationsStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedEndpoint, setSelectedEndpoint] = useState<string>('');
  const [endpoints, setEndpoints] = useState<any[]>([]);

  useEffect(() => {
    loadEndpoints();
  }, []);

  useEffect(() => {
    if (selectedEndpoint) {
      loadOperationsData();
    }
  }, [selectedEndpoint]);

  async function loadEndpoints() {
    try {
      const data = await endpointsApi.list();
      setEndpoints(data.endpoints || []);
      if (data.endpoints && data.endpoints.length > 0) {
        setSelectedEndpoint(data.endpoints[0].id);
      }
    } catch (err: any) {
      setError(err.message);
    }
  }

  async function loadOperationsData() {
    if (!selectedEndpoint) return;
    
    setLoading(true);
    setError(null);

    try {
      // Get endpoint stats
      const statsData = await endpointsApi.getStats(selectedEndpoint);
      
      // Get recent usage logs (we'll need to add this endpoint)
      // For now, use stats data
      const operationsStats: OperationsStats = {
        totalRequests: statsData.stats?.totalRequests || 0,
        successfulRequests: statsData.stats?.totalRequests - (statsData.stats?.totalPayments || 0),
        failedRequests: 0, // Would need to query logs
        totalRevenue: statsData.stats?.totalRevenue || '0',
        averageLatency: statsData.stats?.averageResponseTime || 0,
        requestsByHour: [], // Would need time-series query
        failuresByCode: {}, // Would need to query logs with failure_code
        recentTransactions: (statsData.recentPayments || []).map((p: any) => ({
          txHash: p.txHash,
          timestamp: p.verifiedAt,
          amount: p.amount,
          customerId: p.fromAddress?.substring(0, 10) + '...' || 'unknown',
        })),
      };

      setStats(operationsStats);
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }

  function formatUSDC(amount: string): string {
    // USDC has 6 decimals
    const num = BigInt(amount);
    const decimals = 6;
    const divisor = BigInt(10 ** decimals);
    const whole = num / divisor;
    const fraction = num % divisor;
    return `${whole}.${fraction.toString().padStart(decimals, '0')}`;
  }

  function getBaseExplorerUrl(txHash: string): string {
    return `https://basescan.org/tx/${txHash}`;
  }

  if (loading && !stats) {
    return (
      <div className="min-h-screen bg-gray-50 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-900 mb-8">Operations Dashboard</h1>
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
            <p className="mt-4 text-gray-600">Loading operations data...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Operations Dashboard</h1>
          <p className="text-gray-600">Real-time traffic, failures, and revenue for paid endpoints</p>
        </div>

        {error && (
          <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded mb-6">
            <strong>Error:</strong> {error}
          </div>
        )}

        {/* Endpoint Selector */}
        <div className="mb-6">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Select Endpoint
          </label>
          <select
            value={selectedEndpoint}
            onChange={(e) => setSelectedEndpoint(e.target.value)}
            className="block w-full max-w-md rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
          >
            {endpoints.map((ep) => (
              <option key={ep.id} value={ep.id}>
                {ep.name} ({ep.id})
              </option>
            ))}
          </select>
        </div>

        {stats && (
          <>
            {/* Key Metrics */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
              <div className="bg-white rounded-lg shadow p-6">
                <div className="text-sm font-medium text-gray-500">Total Requests</div>
                <div className="mt-2 text-3xl font-bold text-gray-900">{stats.totalRequests}</div>
              </div>
              <div className="bg-white rounded-lg shadow p-6">
                <div className="text-sm font-medium text-gray-500">Successful</div>
                <div className="mt-2 text-3xl font-bold text-green-600">{stats.successfulRequests}</div>
              </div>
              <div className="bg-white rounded-lg shadow p-6">
                <div className="text-sm font-medium text-gray-500">Total Revenue</div>
                <div className="mt-2 text-3xl font-bold text-blue-600">
                  {formatUSDC(stats.totalRevenue)} USDC
                </div>
              </div>
              <div className="bg-white rounded-lg shadow p-6">
                <div className="text-sm font-medium text-gray-500">Avg Latency</div>
                <div className="mt-2 text-3xl font-bold text-gray-900">
                  {Math.round(stats.averageLatency)}ms
                </div>
              </div>
            </div>

            {/* Recent Transactions */}
            <div className="bg-white rounded-lg shadow mb-8">
              <div className="px-6 py-4 border-b border-gray-200">
                <h2 className="text-xl font-semibold text-gray-900">Recent Transactions</h2>
              </div>
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Transaction Hash
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Amount
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Customer
                      </th>
                      <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Time
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {stats.recentTransactions.length === 0 ? (
                      <tr>
                        <td colSpan={4} className="px-6 py-8 text-center text-gray-500">
                          No transactions yet. Start making paid calls to see them here.
                        </td>
                      </tr>
                    ) : (
                      stats.recentTransactions.map((tx, idx) => (
                        <tr key={idx}>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <a
                              href={getBaseExplorerUrl(tx.txHash)}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="text-blue-600 hover:text-blue-800 font-mono text-sm"
                            >
                              {tx.txHash.substring(0, 16)}...
                            </a>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                            {formatUSDC(tx.amount)} USDC
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 font-mono">
                            {tx.customerId}
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {new Date(tx.timestamp).toLocaleString()}
                          </td>
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </div>

            {/* Failures by Code */}
            {Object.keys(stats.failuresByCode).length > 0 && (
              <div className="bg-white rounded-lg shadow">
                <div className="px-6 py-4 border-b border-gray-200">
                  <h2 className="text-xl font-semibold text-gray-900">Failures by Error Code</h2>
                </div>
                <div className="p-6">
                  <div className="space-y-2">
                    {Object.entries(stats.failuresByCode).map(([code, count]) => (
                      <div key={code} className="flex justify-between items-center">
                        <span className="text-sm font-mono text-gray-700">{code}</span>
                        <span className="text-sm font-semibold text-red-600">{count}</span>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </>
        )}

        {!stats && !loading && (
          <div className="bg-white rounded-lg shadow p-12 text-center">
            <p className="text-gray-600">No operations data available yet.</p>
            <p className="text-sm text-gray-500 mt-2">
              Make some paid calls to your endpoint to see metrics here.
            </p>
          </div>
        )}
      </div>
    </div>
  );
}

