// Auth API (public, no API key required)
export const authApi = {
  signup: async (data: { email: string; password: string; name?: string }) => {
    const response = await fetch('/api/v1/auth/signup', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    });
    const result = await response.json();
    if (!response.ok) {
      throw new ApiError(
        response.status,
        result.code || 'UNKNOWN_ERROR',
        result.error || 'Signup failed',
        result.details
      );
    }
    return result as {
      success: boolean;
      accountId: string;
      plan: { name: string; dailyLimit: number; monthlyLimit: number };
      apiKey: { id: string; name: string; token: string };
      message: string;
    };
  },
};

// Plans API
export const plansApi = {
  // Get all available plans
  list: () => apiRequest<{
    success: boolean;
    plans: Array<{
      id: string;
      name: string;
      description: string | null;
      dailyLimit: number;
      monthlyLimit: number;
      priceCents: number;
      priceDisplay: string;
      features: string[];
    }>;
  }>('/api/v1/user/plans'),

  // Get current plan and usage
  current: () => apiRequest<{
    success: boolean;
    account: { id: string; email: string; status: string };
    plan: { id: string; name: string; dailyLimit: number; monthlyLimit: number };
    usage: {
      daily: { used: number; limit: number; remaining: number; percentUsed: number };
      monthly: { used: number; limit: number; remaining: number; percentUsed: number };
    };
    suggestions: { shouldUpgrade: boolean; reason?: string; suggestedPlan?: string };
  }>('/api/v1/user/current-plan'),

  // Upgrade to a new plan (prototype - no payment)
  upgrade: (planId: 'free' | 'starter' | 'pro' | 'enterprise') => 
    apiRequest<{
      success: boolean;
      message: string;
      previousPlan: { id: string; dailyLimit: number; monthlyLimit: number } | null;
      newPlan: { id: string; name: string; dailyLimit: number; monthlyLimit: number; priceCents: number };
      effectiveImmediately: boolean;
    }>('/api/v1/user/upgrade-plan', {
      method: 'POST',
      body: JSON.stringify({ planId }),
    }),

  // Create USDC payment intent for plan upgrade
  createPaymentIntent: (planId: 'starter' | 'pro') =>
    apiRequest<{
      success: boolean;
      paymentIntent: {
        id: string;
        planId: string;
        amount: { usdc: string; atomic: string; display: string };
        recipient: {
          address: string;
          network: string;
          token: { symbol: string; address: string; decimals: number };
        };
        expiresAt: string;
        expiresInSeconds: number;
      };
      instructions: {
        step1: string;
        step2: string;
        step3: string;
        note: string;
      };
    }>('/api/v1/user/upgrade-plan/payment', {
      method: 'POST',
      body: JSON.stringify({ planId }),
    }),

  // Verify USDC payment and upgrade plan
  verifyPayment: (paymentIntentId: string, txHash: string) =>
    apiRequest<{
      success: boolean;
      message: string;
      plan: { id: string; name: string; dailyLimit: number; monthlyLimit: number };
      payment: { txHash: string; verifiedAt: string; network: string; basescanUrl: string };
    }>('/api/v1/user/upgrade-plan/payment', {
      method: 'PUT',
      body: JSON.stringify({ paymentIntentId, txHash }),
    }),

  // Get payment intent status
  getPaymentStatus: (intentId?: string) =>
    apiRequest<{
      success: boolean;
      intent?: {
        id: string;
        planId: string;
        amountUsdc: string;
        status: string;
        txHash?: string;
        expiresAt: string;
      };
      intents?: Array<{
        id: string;
        planId: string;
        amountUsdc: string;
        status: string;
        expiresAt: string;
      }>;
    }>(`/api/v1/user/upgrade-plan/payment${intentId ? `?intentId=${intentId}` : ''}`),
};

// Cron Jobs API
export interface CronJobSummary {
  jobId: string;
  displayName: string;
  description: string | null;
  schedule: string | null;
  enabled: boolean;
  timeoutMs: number;
  maxRetries: number;
  alertOnFailure: boolean;
  consecutiveFailures: number;
  lastSuccessAt: string | null;
  lastFailureAt: string | null;
  stats: {
    totalRuns: number;
    successRate: number | null;
    avgDurationMs: number | null;
    recentFailures: number;
  };
  lastRun: {
    id: string;
    status: string;
    startedAt: string;
    completedAt: string | null;
    durationMs: number | null;
    triggerSource: string;
    errorMessage: string | null;
  } | null;
}

export interface CronJobRun {
  id: string;
  jobId: string;
  status: 'pending' | 'running' | 'completed' | 'failed' | 'timeout';
  startedAt: string;
  completedAt: string | null;
  durationMs: number | null;
  triggerSource: 'cron' | 'manual' | 'retry';
  attemptNumber: number;
  errorMessage: string | null;
  outputSummary: Record<string, any> | null;
  metadata: Record<string, any> | null;
}

export interface CronJobsResponse {
  success: boolean;
  summary: {
    totalJobs: number;
    activeJobs: number;
    runningJobs: number;
    recentlyFailed: number;
    failingJobsCount: number;
  };
  jobs: CronJobSummary[];
  recentRuns?: CronJobRun[];
  failingJobs?: { jobId: string; displayName: string; consecutiveFailures: number; lastFailureAt: string | null }[];
}

export const cronJobsApi = {
  // Get all cron jobs with stats
  list: (params?: { 
    jobId?: string; 
    limit?: number; 
    includeRuns?: boolean; 
    onlyFailing?: boolean;
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.jobId) searchParams.set('jobId', params.jobId);
    if (params?.limit) searchParams.set('limit', params.limit.toString());
    if (params?.includeRuns === false) searchParams.set('includeRuns', 'false');
    if (params?.onlyFailing) searchParams.set('onlyFailing', 'true');
    const query = searchParams.toString();
    return apiRequest<CronJobsResponse>(`/api/v1/cron-jobs${query ? `?${query}` : ''}`);
  },

  // Manually trigger a job
  trigger: (jobId: string) =>
    apiRequest<{ success: boolean; jobId: string; triggeredAt: string; response: any }>(
      '/api/v1/cron-jobs',
      {
        method: 'POST',
        body: JSON.stringify({ jobId }),
      }
    ),
};

// Onboarding API
export interface OnboardingStep {
  id: string;
  title: string;
  description: string;
  action?: string;
  link?: string;
  docs?: string;
  completed: boolean;
  completedAt: string | null;
  autoComplete?: boolean;
}

export interface OnboardingResponse {
  success: boolean;
  onboarding: {
    isNewUser: boolean;
    showChecklist: boolean;
    progress: { completed: number; total: number; percent: number };
    checklist: OnboardingStep[];
    allComplete: boolean;
    dismissedAt: string | null;
  };
  account: { name: string | null; createdAt: string };
}

export const onboardingApi = {
  // Get onboarding status and checklist
  getStatus: () => apiRequest<OnboardingResponse>('/api/v1/user/onboarding'),

  // Mark a step as complete
  completeStep: (stepId: string) =>
    apiRequest<{ success: boolean; message: string; stepId: string }>('/api/v1/user/onboarding', {
      method: 'PUT',
      body: JSON.stringify({ stepId }),
    }),

  // Dismiss onboarding checklist
  dismiss: () =>
    apiRequest<{ success: boolean; message: string }>('/api/v1/user/onboarding', {
      method: 'PUT',
      body: JSON.stringify({ dismiss: true }),
    }),
};

